import React from "react";
import { useStyletron } from "baseui";
import Slider from "react-slick";
import "./Slider.scss";


import Frame from '../../../Style/Image/Coment/Frame.png'
import Frame1 from '../../../Style/Image/Coment/Frame-1.png'
import Frame4 from '../../../Style/Image/Coment/Frame-4.png'


const demoSlides = [
  {
    name: "Frame",
    src: Frame
  },
  {
    name: "Frame1",
    src: Frame1
  },
  
  {
    name: "Frame4",
    src: Frame4
  },
  
];

const sliderSettings = {
  infinite: true,
  speed: 250,
  slidesToShow: 1,
  slidesToScroll: 1,
  autoplay: true,
  autoplaySpeed: 5000,
  arrow: false
};

const PhoneMockup = ({ children }) => {
  const [css] = useStyletron();

  return (
    <div className="device device-iphone-x" style={{textAlign:"center"}}>
      <div className="device-frame">
        <div
          className={css({
            overflow: "hidden",
            borderRadius: "30px"
          })}
        >
          {children}
        </div>
      </div>
      <div className="device-stripe" />
      <div className="device-header" />
      <div className="device-sensors" />
      <div className="device-btns" />
      <div className="device-power" />
    </div>
  );
};

const CallPageSlider = ({ tilt }) => {
  const [css, theme] = useStyletron();
  const slider = React.createRef();

  return (
    <div
      className={css({
        display: "flex",
        justifyContent: "flex-end",
        position: "relative",
        overflow: "hidden",
        [theme.mediaQuery.maxMedium]: {
          marginRight: "-40px"
        },
        [theme.mediaQuery.maxSmall]: {
          marginRight: "0px",
          justifyContent: "center"
        }
      })}
    >
      <div
        className={css({
          display: "flex",
          alignItems: "center",
          marginRight: "10px"
        })}
      >
        
      </div>
      <div
        className={css({
          transform: tilt
            ? "rotate(3deg) scale(0.9) translate3d(0, 0, 0)"
            : "translate3d(0, 0, 0)",
            
        })}
      >
        <PhoneMockup>
          <Slider {...sliderSettings} ref={slider}>
            {demoSlides.map((slide) => (
              <div key={slide.name}>
                <img
                  src={slide.src}
                  alt={`Callsesh phone demonstration for ${slide.name}`}
                />
              </div>
            ))}
          </Slider>
        </PhoneMockup>
      </div>
      <div
        className={css({
          display: "flex",
          alignItems: "center",
          marginLeft: "10px"
        })}
      >
        
      </div>
    </div>
  );
};

export default function Slide() {
  const [css] = useStyletron();

  return (
    <div
      className={css({
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        width: "100%"
      })}
    >
      <CallPageSlider />
    </div>
  );
}
